.card {
  width: $cardWidth;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", "Helvetica", sans-serif;
  font-weight: 400;

  &:nth-last-child(-n + 4) {
    margin-top: 46px;
  }

  &:not(:nth-child(4n)) {
    margin-right: $marginBetween;
  }

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 3px;
  }

  &__description {
    margin-top: 40px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $mainDark;
  }

  &__code {
    margin-top: 4px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $lightGray;
  }

  &__feedback {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
  }

  &__review {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $mainDark;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $lightGray;
  }

  &__amount {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $mainDark;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16px;

    box-sizing: border-box;
    width: 100%;
    height: 40px;

    color: #fff;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    background: $lightBlue;
    border-radius: 5px;

    &:hover {
      color: $lightBlue;
      background: #fff;
      border: 1px solid $lightBlue;
    }
  }
}

